.logo {
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.logo__box {
  position: relative;

  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 66px;
  padding: 0 @mobile-padding;

  border-bottom: 1px solid #e6e6e6;

  &::after {
    content: "";

    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 101px;
    height: 18px;
    margin: auto;

    background-image: url("../img/logo-text.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.logo__image {
  width: 33px;
  height: 38px;
  margin-top: -5px;
}

@media (min-width: @tablet-width) {
  .logo__box {
    width: auto;
    height: auto;
    padding: 0;

    border-bottom: none;

    &::after {
      content: none;
    }
  }

  .logo__image {
    width: 173px;
    height: 58px;
    margin-top: 0;
  }
}

@media (min-width: @desktop-width) {
  .logo__image {
    width: 202px;
    height: 59px;
  }
}
